/**
  * Grid
  */

// Container
.container {
  margin: 0 auto;
  width: 100%;
}

.banner {
  position: relative;
  float: left;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -20%);
  text-align: center;

  .title {
    display: block;
    padding-bottom: 10px;
  }

  .nav-text {
    font-family: "Roboto Mono", monospace;
    font-weight: 600;
    font-size: 24px;
  }
}

@include media-query($medium-screen) {
  .container {
    width: 85%;
  }
  .banner {
    .title {
      padding-bottom: 20px;
    }
    .nav-text {
      font-size: 32px;
    }
  }
}

@include media-query($large-screen) {
  .container {
    width: 70%;
  }
  .banner {
    .title {
      padding-bottom: 25px;
    }
    .nav-text {
      font-size: 48px;
    }
  }
}

.container .row {
  margin-left: (-1 * $gutter-width / 2);
  margin-right: (-1 * $gutter-width / 2);
}

// Row and colum
.row {
  margin-left: auto;
  margin-right: auto;

  // Clear floating children
  &:after {
    content: "";
    display: table;
    clear: both;
  }

  .col {
    float: left;
    @include box-sizing(border-box);
    display:block;
  	vertical-align:top;
  	width:100%;

    $i: 1;
    @while $i <= $num-cols {
      $perc: unquote((100 / ($num-cols / $i)) + "%");
      &.s#{$i} {
        width: $perc;
        margin-left: 0;
      }
      $i: $i + 1;
    }
    $i: 1;
    @while $i <= $num-cols {
      $perc: unquote((100 / ($num-cols / $i)) + "%");
      &.offset-s#{$i} {
        margin-left: $perc;
      }
      $i: $i + 1;
    }


    @include media-query($small-screen) {

      $i: 1;
      @while $i <= $num-cols {
        $perc: unquote((100 / ($num-cols / $i)) + "%");
        &.m#{$i} {
          width: $perc;
          margin-left: 0;
        }
        $i: $i + 1;
      }
      $i: 1;
      @while $i <= $num-cols {
        $perc: unquote((100 / ($num-cols / $i)) + "%");
        &.offset-m#{$i} {
          margin-left: $perc;
        }
        $i: $i + 1;
      }

    }

    @include media-query($medium-screen) {

      $i: 1;
      @while $i <= $num-cols {
        $perc: unquote((100 / ($num-cols / $i)) + "%");
        &.l#{$i} {
          width: $perc;
          margin-left: 0;
        }
        $i: $i + 1;
      }
      $i: 1;
      @while $i <= $num-cols {
        $perc: unquote((100 / ($num-cols / $i)) + "%");
        &.offset-l#{$i} {
          margin-left: $perc;
        }
        $i: $i + 1;
      }

    }

  }
}

.nav-list-masthead {
  margin: 1.9em 0;
  padding: 0 1em;
  list-style: none;
  .logo-img {
    display: block;
    width: 72px;
    height: 72px;
    float: left;
    img {
      -webkit-filter: opacity(50%);
      filter: opacity(50%);
      &:hover,
      &:focus {
        filter: opacity(100%);
      }
    }
  }
  .bin-clock {
    display: block;
    float: right;
    font-size: 13px;
    font-family: $font-family-sans-serif;
   }
}
.image-credit {
  float: right;
  font-weight: bold;
  font-style: italic;
  color: $info;
  font-size: 13px;
  padding-right: 20px;
}

// Post title
.post-title {
    font-family: $font-family-base;
    font-size: 2.5em;
    margin-bottom: 10px;
}

.post-title-link {
	color: $primary;
	font-size: 14px;
	margin-left: 10px;
	&:hover,
	&:focus {
		color: lighten($primary, 14%);
	}
}

// Post description
.post-description {
    font-family: $font-family-serif-2;
    font-size: 1.2em;
}

// Post meta
.post-meta {
  font-family: $font-family-monospace;
  font-size: 13px;
  font-weight: bold;
	.post-date {
		color: $secondary;
	}
	.post-author {
		text-transform: uppercase;
		color: $warning;
	}
  .post-cat {
		text-transform: uppercase;
		color: $warning;
	}
  .read-time {
    color: $primary;
  }
}

// Read more buttons

a.read-more {
  padding: 5px 8px;
}

// Post content

.post-content {
  font-family: $font-family-serif-2;
  p {
    font-size: 1.6rem;
  }
}

#post {
  .post-header {
    font-size: 18px;
    margin-bottom: 30px;
  }
}

#post h1 {
  margin: 0.5em 0 1em;
}
#post h2 {
  margin: 2em 0 0.8em;
  padding-bottom: 0.7em;
  border-bottom: 1px solid #ddd;
}
#post h3 {
  margin: 1.75em 0 1.2em;
  position: relative;
}

#page {
  padding-bottom: 50px;
  min-height: 70vh;
  .page-header {
    margin: 0 auto 50px;
  }
}
.post-image-feature {
  margin-bottom: 20px;
  padding: 20px;
  width: 60%;
}

.feature-image {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
  padding: 20px;
}

// Shares Buttons
.share-buttons {
  margin: 0 1rem;
  padding-top: 1em;
  border-top: 1px solid #ddd;
  ol {
    margin: 0;
    padding: 0;
    list-style: none;
    li {
      display: inline-block;
      margin-right: 0.5em;
      span {
        display: none;
        @include media-query($small-screen) {
          display: inline;
          margin: 0.5em;
        }
      }
    }
  }
}
